const dateParse = {
  // -- dateToString
  dateToString: function (param) {
    const newDate = new Date(param);
    const year = newDate.getFullYear();
    const month =
      newDate.getMonth() < 9
        ? "0" + String(newDate.getMonth() + 1)
        : String(newDate.getMonth() + 1);
    const date =
      newDate.getDate() < 10
        ? "0" + String(newDate.getDate())
        : String(newDate.getDate());
    return `${year}-${month}-${date}`;
  },
  dateToString2: function (param) {
    const newDate = new Date(param);
    const year = newDate.getFullYear();
    const month =
      newDate.getMonth() < 9
        ? "0" + String(newDate.getMonth() + 1)
        : String(newDate.getMonth() + 1);
    const date =
      newDate.getDate() < 10
        ? "0" + String(newDate.getDate())
        : String(newDate.getDate());
    return `${year}${month}${date}`;
  },
  // -- // dateToString
};

export default dateParse;
